// pages/GestaoOficina.js
import React, { useState } from 'react';
import './GestaoOficina.css';

function GestaoOficina() {
  const [veiculos, setVeiculos] = useState([]);
  const [novoVeiculo, setNovoVeiculo] = useState({ placa: '', modelo: '', cliente: '' });
  const [agendamentos, setAgendamentos] = useState([]);
  const [novoAgendamento, setNovoAgendamento] = useState({ data: '', hora: '', cliente: '', comissao: '' });
  const [historico, setHistorico] = useState([]);

  const handleVeiculoSubmit = (e) => {
    e.preventDefault();
    setVeiculos([...veiculos, novoVeiculo]);
    setNovoVeiculo({ placa: '', modelo: '', cliente: '' });
  };

  const handleAgendamentoSubmit = (e) => {
    e.preventDefault();
    setAgendamentos([...agendamentos, novoAgendamento]);
    setNovoAgendamento({ data: '', hora: '', cliente: '', comissao: '' });
  };

  return (
    <div className="gestao-oficina">
      <h2>Gestão de Oficina</h2>

      {/* Cadastro de Veículos */}
      <section>
        <h3>Cadastro de Veículos</h3>
        <form onSubmit={handleVeiculoSubmit}>
          <input
            type="text"
            placeholder="Placa"
            value={novoVeiculo.placa}
            onChange={(e) => setNovoVeiculo({ ...novoVeiculo, placa: e.target.value })}
          />
          <input
            type="text"
            placeholder="Modelo"
            value={novoVeiculo.modelo}
            onChange={(e) => setNovoVeiculo({ ...novoVeiculo, modelo: e.target.value })}
          />
          <input
            type="text"
            placeholder="Cliente"
            value={novoVeiculo.cliente}
            onChange={(e) => setNovoVeiculo({ ...novoVeiculo, cliente: e.target.value })}
          />
          <button type="submit">Adicionar Veículo</button>
        </form>
        <ul>
          {veiculos.map((veiculo, index) => (
            <li key={index}>
              Placa: {veiculo.placa}, Modelo: {veiculo.modelo}, Cliente: {veiculo.cliente}
            </li>
          ))}
        </ul>
      </section>

      {/* Controle de Agendamentos */}
      <section>
        <h3>Controle de Agendamentos</h3>
        <form onSubmit={handleAgendamentoSubmit}>
          <input
            type="date"
            value={novoAgendamento.data}
            onChange={(e) => setNovoAgendamento({ ...novoAgendamento, data: e.target.value })}
          />
          <input
            type="time"
            value={novoAgendamento.hora}
            onChange={(e) => setNovoAgendamento({ ...novoAgendamento, hora: e.target.value })}
          />
          <input
            type="text"
            placeholder="Cliente"
            value={novoAgendamento.cliente}
            onChange={(e) => setNovoAgendamento({ ...novoAgendamento, cliente: e.target.value })}
          />
          <input
            type="number"
            placeholder="Comissão (R$)"
            value={novoAgendamento.comissao}
            onChange={(e) => setNovoAgendamento({ ...novoAgendamento, comissao: e.target.value })}
          />
          <button type="submit">Adicionar Agendamento</button>
        </form>
        <ul>
          {agendamentos.map((agendamento, index) => (
            <li key={index}>
              Data: {agendamento.data}, Hora: {agendamento.hora}, Cliente: {agendamento.cliente}, Comissão: R$
              {agendamento.comissao}
            </li>
          ))}
        </ul>
      </section>

      {/* Histórico Detalhado */}
      <section>
        <h3>Histórico de Veículos</h3>
        <table>
          <thead>
            <tr>
              <th>Placa</th>
              <th>Serviço Realizado</th>
              <th>Data</th>
            </tr>
          </thead>
          <tbody>
            {historico.map((item, index) => (
              <tr key={index}>
                <td>{item.placa}</td>
                <td>{item.servico}</td>
                <td>{item.data}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>
    </div>
  );
}

export default GestaoOficina;
