// pages/Estoque.js
import React, { useState } from 'react';
import './Estoque.css';

function Estoque() {
  const [produtos, setProdutos] = useState([]);
  const [novoProduto, setNovoProduto] = useState({
    nome: '',
    quantidade: '',
    garantia: '',
  });

  const [historico, setHistorico] = useState([]);

  const handleProdutoSubmit = (e) => {
    e.preventDefault();
    const produtoExistente = produtos.find((p) => p.nome === novoProduto.nome);
    if (produtoExistente) {
      produtoExistente.quantidade += parseInt(novoProduto.quantidade, 10);
      setProdutos([...produtos]);
    } else {
      setProdutos([...produtos, { ...novoProduto, quantidade: parseInt(novoProduto.quantidade, 10), id: Date.now() }]);
    }

    setHistorico([
      ...historico,
      {
        tipo: 'Entrada',
        produto: novoProduto.nome,
        quantidade: novoProduto.quantidade,
        data: new Date().toLocaleString(),
      },
    ]);
    setNovoProduto({ nome: '', quantidade: '', garantia: '' });
  };

  const handleSaidaProduto = (produto) => {
    const quantidade = prompt(`Quantidade para saída do produto "${produto.nome}":`);
    if (quantidade && quantidade > 0) {
      produto.quantidade -= parseInt(quantidade, 10);
      setProdutos([...produtos]);

      setHistorico([
        ...historico,
        {
          tipo: 'Saída',
          produto: produto.nome,
          quantidade,
          data: new Date().toLocaleString(),
        },
      ]);
    }
  };

  const imprimirEtiqueta = (produto) => {
    alert(`Etiqueta impressa para: ${produto.nome}\nGarantia: ${produto.garantia}`);
  };

  return (
    <div className="estoque">
      <h2>Controle de Estoque</h2>

      {/* Controle e Adição de Produtos */}
      <section>
        <h3>Adicionar Produto</h3>
        <form onSubmit={handleProdutoSubmit}>
          <input
            type="text"
            placeholder="Nome do Produto"
            value={novoProduto.nome}
            onChange={(e) => setNovoProduto({ ...novoProduto, nome: e.target.value })}
            required
          />
          <input
            type="number"
            placeholder="Quantidade"
            value={novoProduto.quantidade}
            onChange={(e) => setNovoProduto({ ...novoProduto, quantidade: e.target.value })}
            required
          />
          <input
            type="text"
            placeholder="Garantia (Ex.: 1 ano)"
            value={novoProduto.garantia}
            onChange={(e) => setNovoProduto({ ...novoProduto, garantia: e.target.value })}
          />
          <button type="submit">Adicionar</button>
        </form>

        <h3>Estoque Atual</h3>
        <ul>
          {produtos.map((produto) => (
            <li key={produto.id}>
              {produto.nome} | Quantidade: {produto.quantidade} | Garantia: {produto.garantia}
              <button onClick={() => handleSaidaProduto(produto)}>Registrar Saída</button>
              <button onClick={() => imprimirEtiqueta(produto)}>Imprimir Etiqueta</button>
            </li>
          ))}
        </ul>
      </section>

      {/* Histórico de Movimentações */}
      <section>
        <h3>Histórico de Movimentações</h3>
        <ul>
          {historico.map((item, index) => (
            <li key={index}>
              [{item.data}] {item.tipo}: {item.produto} | Quantidade: {item.quantidade}
            </li>
          ))}
        </ul>
      </section>
    </div>
  );
}

export default Estoque;
