// pages/ControleFinanceiro.js
import React, { useState } from 'react';
import './ControleFinanceiro.css';

function ControleFinanceiro() {
  const [transacoes, setTransacoes] = useState([]);
  const [novaTransacao, setNovaTransacao] = useState({
    tipo: 'Receita',
    descricao: '',
    valor: '',
    metodo: 'Dinheiro',
  });

  const handleTransacaoSubmit = (e) => {
    e.preventDefault();
    setTransacoes([...transacoes, { ...novaTransacao, id: Date.now() }]);
    setNovaTransacao({ tipo: 'Receita', descricao: '', valor: '', metodo: 'Dinheiro' });
  };

  const calcularMargem = () => {
    const receitas = transacoes.filter((t) => t.tipo === 'Receita').reduce((acc, curr) => acc + Number(curr.valor), 0);
    const despesas = transacoes.filter((t) => t.tipo === 'Despesa').reduce((acc, curr) => acc + Number(curr.valor), 0);
    return receitas - despesas;
  };

  return (
    <div className="controle-financeiro">
      <h2>Controle Financeiro</h2>

      {/* Cadastro de Transações */}
      <section>
        <h3>Adicionar Transação</h3>
        <form onSubmit={handleTransacaoSubmit}>
          <select
            value={novaTransacao.tipo}
            onChange={(e) => setNovaTransacao({ ...novaTransacao, tipo: e.target.value })}
          >
            <option value="Receita">Receita</option>
            <option value="Despesa">Despesa</option>
          </select>
          <input
            type="text"
            placeholder="Descrição"
            value={novaTransacao.descricao}
            onChange={(e) => setNovaTransacao({ ...novaTransacao, descricao: e.target.value })}
          />
          <input
            type="number"
            placeholder="Valor (R$)"
            value={novaTransacao.valor}
            onChange={(e) => setNovaTransacao({ ...novaTransacao, valor: e.target.value })}
          />
          <select
            value={novaTransacao.metodo}
            onChange={(e) => setNovaTransacao({ ...novaTransacao, metodo: e.target.value })}
          >
            <option value="Dinheiro">Dinheiro</option>
            <option value="Cheque">Cheque</option>
            <option value="Cartão">Cartão</option>
            <option value="Comissão">Comissão</option>
          </select>
          <button type="submit">Adicionar</button>
        </form>
      </section>

      {/* Lista de Transações */}
      <section>
        <h3>Transações</h3>
        <table>
          <thead>
            <tr>
              <th>Tipo</th>
              <th>Descrição</th>
              <th>Valor</th>
              <th>Método</th>
            </tr>
          </thead>
          <tbody>
            {transacoes.map((transacao) => (
              <tr key={transacao.id}>
                <td>{transacao.tipo}</td>
                <td>{transacao.descricao}</td>
                <td>R$ {Number(transacao.valor).toFixed(2)}</td>
                <td>{transacao.metodo}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>

      {/* Fluxo de Caixa e Margem de Lucro */}
      <section>
        <h3>Resumo Financeiro</h3>
        <p>
          Margem de Lucro: <strong>R$ {calcularMargem().toFixed(2)}</strong>
        </p>
        <p>Total de Receitas: R${' '}
          {transacoes
            .filter((t) => t.tipo === 'Receita')
            .reduce((acc, curr) => acc + Number(curr.valor), 0)
            .toFixed(2)}
        </p>
        <p>Total de Despesas: R${' '}
          {transacoes
            .filter((t) => t.tipo === 'Despesa')
            .reduce((acc, curr) => acc + Number(curr.valor), 0)
            .toFixed(2)}
        </p>
      </section>
    </div>
  );
}

export default ControleFinanceiro;
