// pages/Cadastros.js
import React, { useState } from 'react';
import './Cadastros.css';

function Cadastros() {
  const [clientes, setClientes] = useState([]);
  const [novoCliente, setNovoCliente] = useState({
    nome: '',
    telefone: '',
    email: '',
    servicos: [],
  });

  const [estoque, setEstoque] = useState([]);
  const [novoItem, setNovoItem] = useState({
    nome: '',
    quantidade: '',
    reposicaoMinima: '',
  });

  const handleClienteSubmit = (e) => {
    e.preventDefault();
    setClientes([...clientes, { ...novoCliente, id: Date.now() }]);
    setNovoCliente({ nome: '', telefone: '', email: '', servicos: [] });
  };

  const handleServicoSubmit = (clienteId, e) => {
    e.preventDefault();
    const clienteIndex = clientes.findIndex((cliente) => cliente.id === clienteId);
    const cliente = clientes[clienteIndex];
    cliente.servicos.push({ descricao: e.target[0].value, data: new Date().toLocaleDateString() });
    setClientes([...clientes]);
  };

  const handleItemSubmit = (e) => {
    e.preventDefault();
    setEstoque([...estoque, { ...novoItem, id: Date.now() }]);
    setNovoItem({ nome: '', quantidade: '', reposicaoMinima: '' });
  };

  return (
    <div className="cadastros">
      <h2>Cadastros</h2>

      {/* Cadastro de Cliente */}
      <section>
        <h3>Cadastro de Cliente</h3>
        <form onSubmit={handleClienteSubmit}>
          <input
            type="text"
            placeholder="Nome do Cliente"
            value={novoCliente.nome}
            onChange={(e) => setNovoCliente({ ...novoCliente, nome: e.target.value })}
            required
          />
          <input
            type="text"
            placeholder="Telefone"
            value={novoCliente.telefone}
            onChange={(e) => setNovoCliente({ ...novoCliente, telefone: e.target.value })}
            required
          />
          <input
            type="email"
            placeholder="Email"
            value={novoCliente.email}
            onChange={(e) => setNovoCliente({ ...novoCliente, email: e.target.value })}
            required
          />
          <button type="submit">Cadastrar Cliente</button>
        </form>

        <h3>Clientes Cadastrados</h3>
        <ul>
          {clientes.map((cliente) => (
            <li key={cliente.id}>
              {cliente.nome} | {cliente.telefone} | {cliente.email}
              <ul>
                {cliente.servicos.map((servico, index) => (
                  <li key={index}>
                    {servico.descricao} | {servico.data}
                  </li>
                ))}
              </ul>
              <form onSubmit={(e) => handleServicoSubmit(cliente.id, e)}>
                <input
                  type="text"
                  placeholder="Descrição do Serviço"
                  required
                />
                <button type="submit">Adicionar Serviço</button>
              </form>
            </li>
          ))}
        </ul>
      </section>

      {/* Cadastro de Estoque de Peças e Materiais */}
      <section>
        <h3>Cadastro de Estoque</h3>
        <form onSubmit={handleItemSubmit}>
          <input
            type="text"
            placeholder="Nome do Item"
            value={novoItem.nome}
            onChange={(e) => setNovoItem({ ...novoItem, nome: e.target.value })}
            required
          />
          <input
            type="number"
            placeholder="Quantidade"
            value={novoItem.quantidade}
            onChange={(e) => setNovoItem({ ...novoItem, quantidade: e.target.value })}
            required
          />
          <input
            type="number"
            placeholder="Reposição Mínima"
            value={novoItem.reposicaoMinima}
            onChange={(e) => setNovoItem({ ...novoItem, reposicaoMinima: e.target.value })}
            required
          />
          <button type="submit">Adicionar Item</button>
        </form>

        <h3>Estoque Atual</h3>
        <ul>
          {estoque.map((item) => (
            <li key={item.id} style={{ color: item.quantidade <= item.reposicaoMinima ? 'red' : 'black' }}>
              {item.nome} | Quantidade: {item.quantidade} | Reposição Mínima: {item.reposicaoMinima}
              {item.quantidade <= item.reposicaoMinima && <span style={{ color: 'red' }}> - Reposição Necessária!</span>}
            </li>
          ))}
        </ul>
      </section>
    </div>
  );
}

export default Cadastros;
