// pages/VendasCompras.js
import React, { useState } from 'react';
import './VendasCompras.css';

function VendasCompras() {
  const [cupons, setCupons] = useState([]);
  const [novoCupom, setNovoCupom] = useState({
    cliente: '',
    valor: '',
  });

  const [comissoes, setComissoes] = useState([]);
  const [novaComissao, setNovaComissao] = useState({
    vendedor: '',
    valor: '',
  });

  const [compras, setCompras] = useState([]);
  const [novaCompra, setNovaCompra] = useState({
    fornecedor: '',
    produto: '',
    valor: '',
  });

  const [fornecedores, setFornecedores] = useState([
    { nome: 'Fornecedor A', contato: '1234-5678' },
    { nome: 'Fornecedor B', contato: '9876-5432' },
  ]);

  const handleCupomSubmit = (e) => {
    e.preventDefault();
    setCupons([...cupons, { ...novoCupom, id: Date.now() }]);
    setNovoCupom({ cliente: '', valor: '' });
  };

  const handleComissaoSubmit = (e) => {
    e.preventDefault();
    setComissoes([...comissoes, { ...novaComissao, id: Date.now() }]);
    setNovaComissao({ vendedor: '', valor: '' });
  };

  const handleCompraSubmit = (e) => {
    e.preventDefault();
    setCompras([...compras, { ...novaCompra, id: Date.now() }]);
    setNovaCompra({ fornecedor: '', produto: '', valor: '' });
  };

  return (
    <div className="vendas-compras">
      <h2>Vendas e Compras</h2>

      {/* Emissão de Cupom Fiscal */}
      <section>
        <h3>Emissão de Cupom Fiscal</h3>
        <form onSubmit={handleCupomSubmit}>
          <input
            type="text"
            placeholder="Nome do Cliente"
            value={novoCupom.cliente}
            onChange={(e) => setNovoCupom({ ...novoCupom, cliente: e.target.value })}
          />
          <input
            type="number"
            placeholder="Valor da Venda (R$)"
            value={novoCupom.valor}
            onChange={(e) => setNovoCupom({ ...novoCupom, valor: e.target.value })}
          />
          <button type="submit">Emitir Cupom</button>
        </form>
        <ul>
          {cupons.map((cupom) => (
            <li key={cupom.id}>
              Cliente: {cupom.cliente} | Valor: R$ {Number(cupom.valor).toFixed(2)}
            </li>
          ))}
        </ul>
      </section>

      {/* Controle de Comissões */}
      <section>
        <h3>Controle de Comissões</h3>
        <form onSubmit={handleComissaoSubmit}>
          <input
            type="text"
            placeholder="Nome do Vendedor"
            value={novaComissao.vendedor}
            onChange={(e) => setNovaComissao({ ...novaComissao, vendedor: e.target.value })}
          />
          <input
            type="number"
            placeholder="Valor da Comissão (R$)"
            value={novaComissao.valor}
            onChange={(e) => setNovaComissao({ ...novaComissao, valor: e.target.value })}
          />
          <button type="submit">Registrar Comissão</button>
        </form>
        <ul>
          {comissoes.map((comissao) => (
            <li key={comissao.id}>
              Vendedor: {comissao.vendedor} | Valor: R$ {Number(comissao.valor).toFixed(2)}
            </li>
          ))}
        </ul>
      </section>

      {/* Solicitação e Histórico de Compras */}
      <section>
        <h3>Solicitação e Histórico de Compras</h3>
        <form onSubmit={handleCompraSubmit}>
          <input
            type="text"
            placeholder="Fornecedor"
            value={novaCompra.fornecedor}
            onChange={(e) => setNovaCompra({ ...novaCompra, fornecedor: e.target.value })}
          />
          <input
            type="text"
            placeholder="Produto"
            value={novaCompra.produto}
            onChange={(e) => setNovaCompra({ ...novaCompra, produto: e.target.value })}
          />
          <input
            type="number"
            placeholder="Valor (R$)"
            value={novaCompra.valor}
            onChange={(e) => setNovaCompra({ ...novaCompra, valor: e.target.value })}
          />
          <button type="submit">Registrar Compra</button>
        </form>
        <ul>
          {compras.map((compra) => (
            <li key={compra.id}>
              Fornecedor: {compra.fornecedor} | Produto: {compra.produto} | Valor: R$ {Number(compra.valor).toFixed(2)}
            </li>
          ))}
        </ul>
      </section>

      {/* Lista de Fornecedores */}
      <section>
        <h3>Lista de Fornecedores</h3>
        <ul>
          {fornecedores.map((fornecedor, index) => (
            <li key={index}>
              Nome: {fornecedor.nome} | Contato: {fornecedor.contato}
            </li>
          ))}
        </ul>
      </section>
    </div>
  );
}

export default VendasCompras;
