// pages/RelatoriosPersonalizaveis.js
import React, { useState } from 'react';
import { Calendar, dateFnsLocalizer } from 'react-big-calendar';
import { format, parse, startOfWeek, getDay } from 'date-fns';
import "react-big-calendar/lib/css/react-big-calendar.css";
import './RelatoriosPersonalizaveis.css';

const locales = {
  "pt-BR": require("date-fns/locale/pt-BR"),
};

const localizer = dateFnsLocalizer({
  format,
  parse,
  startOfWeek,
  getDay,
  locales,
});

function RelatoriosPersonalizaveis() {
  const [relatorios, setRelatorios] = useState([]);
  const [filtros, setFiltros] = useState({ tipo: '', periodo: '' });
  const [eventos, setEventos] = useState([]);
  const [novoEvento, setNovoEvento] = useState({ title: '', start: '', end: '' });

  const handleRelatorioSubmit = (e) => {
    e.preventDefault();
    const novoRelatorio = `Relatório: ${filtros.tipo} | Período: ${filtros.periodo}`;
    setRelatorios([...relatorios, novoRelatorio]);
  };

  const handleNovoEvento = () => {
    setEventos([...eventos, { ...novoEvento, id: Date.now() }]);
    setNovoEvento({ title: '', start: '', end: '' });
  };

  return (
    <div className="relatorios-personalizaveis">
      <h2>Relatórios Personalizáveis</h2>

      {/* Relatórios Customizáveis */}
      <section>
        <h3>Gerar Relatório</h3>
        <form onSubmit={handleRelatorioSubmit}>
          <select
            value={filtros.tipo}
            onChange={(e) => setFiltros({ ...filtros, tipo: e.target.value })}
          >
            <option value="">Selecione o tipo</option>
            <option value="Financeiro">Financeiro</option>
            <option value="Oficina">Oficina</option>
            <option value="Estoque">Estoque</option>
          </select>
          <input
            type="text"
            placeholder="Período (Ex.: Janeiro 2024)"
            value={filtros.periodo}
            onChange={(e) => setFiltros({ ...filtros, periodo: e.target.value })}
          />
          <button type="submit">Gerar Relatório</button>
        </form>
        <ul>
          {relatorios.map((relatorio, index) => (
            <li key={index}>{relatorio}</li>
          ))}
        </ul>
      </section>

      {/* Agendamento com Calendário */}
      <section>
        <h3>Agendamento de Serviços</h3>
        <div className="calendar-container">
          <Calendar
            localizer={localizer}
            events={eventos}
            startAccessor="start"
            endAccessor="end"
            style={{ height: 400, margin: "20px 0" }}
          />
        </div>
        <div className="evento-form">
          <input
            type="text"
            placeholder="Título do Evento"
            value={novoEvento.title}
            onChange={(e) => setNovoEvento({ ...novoEvento, title: e.target.value })}
          />
          <input
            type="datetime-local"
            placeholder="Data de Início"
            value={novoEvento.start}
            onChange={(e) => setNovoEvento({ ...novoEvento, start: e.target.value })}
          />
          <input
            type="datetime-local"
            placeholder="Data de Término"
            value={novoEvento.end}
            onChange={(e) => setNovoEvento({ ...novoEvento, end: e.target.value })}
          />
          <button onClick={handleNovoEvento}>Adicionar Evento</button>
        </div>
      </section>
    </div>
  );
}

export default RelatoriosPersonalizaveis;
