// App.js
import React from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import './App.css';

// Importando as páginas
import GestaoOficina from './pages/GestaoOficina';
import ControleFinanceiro from './pages/ControleFinanceiro';
import RelatoriosPersonalizaveis from './pages/RelatoriosPersonalizaveis';
import VendasCompras from './pages/VendasCompras';
import Estoque from './pages/Estoque';
import Cadastros from './pages/Cadastros';

function App() {
  return (
    <Router>
      <div className="App">
        <header>
          <h1>Gestão de Oficina</h1>
          <nav>
            <ul>
              <li><Link to="/">Início</Link></li>
              <li><Link to="/gestao-oficina">Gestão de Oficina</Link></li>
              <li><Link to="/controle-financeiro">Controle Financeiro</Link></li>
              <li><Link to="/relatorios-personalizaveis">Relatórios</Link></li>
              <li><Link to="/vendas-compras">Vendas e Compras</Link></li>
              <li><Link to="/estoque">Estoque</Link></li>
              <li><Link to="/cadastros">Cadastros</Link></li>
            </ul>
          </nav>
        </header>

        <main>
          <Routes>
            <Route path="/" element={<h2>Bem-vindo ao Sistema de Gestão de Oficina</h2>} />
            <Route path="/gestao-oficina" element={<GestaoOficina />} />
            <Route path="/controle-financeiro" element={<ControleFinanceiro />} />
            <Route path="/relatorios-personalizaveis" element={<RelatoriosPersonalizaveis />} />
            <Route path="/vendas-compras" element={<VendasCompras />} />
            <Route path="/estoque" element={<Estoque />} />
            <Route path="/cadastros" element={<Cadastros />} />
          </Routes>
        </main>
      </div>
    </Router>
  );
}

export default App;
